  height: 100vh;
  width: 100%;
  //   background: url("../../image/saotroi.jpg");
  background-image: url("data:image/jpeg;base64,");
  background-color: #cccccc;
  background-size: cover;
  color: white;
  .header-scores {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    font-size: 30px;
  }
  .body {
    display: flex;
    justify-content: space-evenly;
    gap: 5px;
    color: black;
    .body-item {
      //   border: 1px solid red;
      background-color: gold;
      width: 310px;
      border-radius: 5px;
      .body-title {
        padding: 16px 18px;
        font-weight: 600;
      }
      .body-image {
        // background-image: url("data:image/png;base64,");
        // background-color: #cccccc;
        // background-size: cover;

        img {
          height: 200px;
          width: 100%;
          object-fit: fill;
        }
      }
      .body-list {
        margin-top: -4px;
        background-color: white;
        padding: 18px;
        font-size: 15px;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        .item {
          margin: 15px 0px;
          background-color: gold;
          padding: 5px;
          border-radius: 5px;
          display: flex;
          justify-content: space-between;
        }
      }
    }
  }
